import React from 'react';

import { Row, Col, Card, Form, Collapse } from 'react-bootstrap';

import AsyncSelect from 'react-select/async';
import { withRouter } from 'react-router-dom';
import isEmpty from '../../../validation/isEmpty';
import { getItemCatagories } from '../../../api/inventory/itemCategory';
import Jhilikka from '../Reusables/Jhilikka';
import DatePicker from 'react-datepicker';
import { getCustomers } from '../../../api/sales/customer';
import { getInvoiceStatus } from '../../../api/sales/invoice';

const filterDataModel = {
    invoiceNo: '',
    dateFrom: null,
    dateUpto: null,
    customer: '',
    customerName: '',
    invoiceStatus: '',
    isActive: '',
    isCanceled: '',
}

function InvoicesFilter(props) {

    const [showFilters, setShowFilters] = React.useState(false);

    const [invoiceStatuses, setInvoiceStatuses] = React.useState([])

    const [filterData, setFilterData] = React.useState({ ...filterDataModel });

    //extract the query params to fill the filter's form
    React.useEffect(() => {
        getInvoiceStatus()
            .then((res) => {
                if (res.json.status) {
                    setInvoiceStatuses(res.json.data);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });

        const queryString = props.location.search;
        const queryObj = new URLSearchParams(queryString) || '';
        let invoiceNo = queryObj.get('invoiceNo') || '';
        let dateFrom = queryObj.get('dateFrom') ? new Date(queryObj.get('dateFrom')) : '';
        let dateUpto = queryObj.get('dateUpto') ? new Date(queryObj.get('dateUpto')) : '';
        let customer = queryObj.get('customer') || '';
        let customerName = queryObj.get('customerName') || '';
        let invoiceStatus = queryObj.get('invoiceStatus') || '';
        let isCanceled = queryObj.get('isCanceled') || '';
        let isActive = queryObj.get('isActive') || '';

        setFilterData({
            ...filterData,
            invoiceNo,
            dateFrom,
            dateUpto,
            customer,
            customerName,
            invoiceStatus,
            isCanceled,
            isActive,
        });
    }, [])

    const loadCustomerOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getCustomers(requestJson)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    return res.json.customers.map((ven, index) => ({ label: `${ven.first_name} ${ven.middle_name || ''} ${ven.last_name}`, value: ven.id }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }

    const _selectedCustomerChange = (customer) => {
        setFilterData({
            ...filterData,
            customer: customer ? customer.value : '',
            customerName: customer ? customer.label : '',
            invoiceNo: '',
        });

    }


    const _dateFromChange = (selectedDate) => {
        setFilterData({ ...filterData, dateFrom: selectedDate, invoiceNo: '', })
    }

    const _dateUptoChange = (selectedDate) => {
        setFilterData({ ...filterData, dateUpto: selectedDate, invoiceNo: '' })
    }

    const _filterDataChange = (e) => {
        const { name, value, type } = e.target;
        let tempFilterData = { ...filterData }
        tempFilterData = { ...tempFilterData, [name]: (type === 'number' ? parseFloat(value) : value) }
        if (name !== 'invoiceNo') {
            tempFilterData = { ...tempFilterData, invoiceNo: '' }
        }

        setFilterData(tempFilterData)
    }




    const _keypress = (e) => {
        if (e.key === 'Enter') {
            _search();
        }
    }


    const _search = () => {
        //append the query params
        let queryParams = new URLSearchParams();
        for (let key in filterData) {
            if (filterData[key]) {

                if (key === 'dateFrom' || key === 'dateUpto') {
                    queryParams.append(key, filterData[key].toISOString())
                } else if (key === 'invoiceNo') {
                    queryParams = new URLSearchParams();
                    queryParams.append('invoiceNo', filterData[key]);
                    break;
                } else {
                    queryParams.append(key, filterData[key])
                }
            }
        }
        let queryString = queryParams.toString();
        console.log(queryString)
        props.history.replace({ search: queryString });
    }

    const _resetFilters = () => {
        setFilterData({ ...filterDataModel });
        props.history.replace();
    }

    //check if there are extra filters
    const NotificationDot = <div style={{
        background: "#a6c0ce",
        width: "12px",
        height: "12px",
        borderRadius: "50%",
        position: "relative",
        top: "-5px",
        left: "-7px",
    }}
        className="itemsSearchFilterNotificationDot"
    ></div>

    let showNotificationDot = false;
    for (let key in filterData) {
        if (key !== 'invoiceNo') {
            if (!isEmpty((filterData[key] || '').toString())) {
                console.log(key)
                showNotificationDot = true;
            }
        }
    }

    return (
        <>
            <Card.Header className="filterInputs" onKeyDown={_keypress}>
                <div className="filterHeader" >
                    <div style={{ display: 'flex', flex: 9 }}>
                        <Form.Control
                            name="invoiceNo"
                            value={filterData.invoiceNo}
                            onChange={_filterDataChange}
                            size="sm"
                            type="text"
                            placeholder="Invoice Number"
                            className="mainSearchBox"
                        />

                        <button
                            className="btn btn-secondary btn-sm"
                            onClick={_search}
                            style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                        > <i className="feather icon-search text-c-white f-8 mr-1" />Search</button>
                    </div>

                    <button
                        className="btn btn-secondary btn-sm showFilterButton"
                        onClick={() => setShowFilters(!showFilters)}

                    >
                        <div className="filterWrapper">
                            <i className="feather icon-sliders text-c-white f-8" />
                        </div>
                        <span>{showFilters ? "Hide" : "Show"} Filters
                        </span>

                    </button>
                    {showNotificationDot ? NotificationDot : null}

                </div>

                <Collapse in={showFilters}>
                    <div id="basic-collapse" >
                        <Card.Body className="filterBox pr-3 pl-3 pb-0">


                            <Row>
                                <Col md={3}>
                                    <Form.Label>Interval</Form.Label>
                                    <div style={{ display: 'flex' }}>
                                        <DatePicker
                                            onChange={_dateFromChange}
                                            placeholderText="Date From"
                                            name="dateFrom"
                                            selected={filterData.dateFrom}
                                        />
                                        <div style={{ width: '8px' }}></div>
                                        <DatePicker
                                            onChange={_dateUptoChange}
                                            placeholderText="Date Upto"
                                            name="dateUpto"
                                            selected={filterData.dateUpto}
                                        />
                                    </div>
                                </Col>


                                <Col className="filterSelectInput" md={3}>
                                    <Form.Label>Customer</Form.Label>
                                    <AsyncSelect
                                        value={isEmpty(filterData.customer) ? null : { label: filterData.customerName, value: filterData.category }}
                                        onChange={(customer) => _selectedCustomerChange(customer)}
                                        loadOptions={loadCustomerOptions}
                                        isClearable={true}
                                        placeholder='Start typing'
                                    />
                                </Col>
                                <Col md={3}>
                                    <Form.Label>Status</Form.Label>
                                    <Form.Control name='invoiceStatus' type="number" value={filterData.invoiceStatus} onChange={_filterDataChange} as="select">

                                        <option value='' >None</option>
                                        {invoiceStatuses.map((item, idex) => {
                                            return <option key={idex} value={item.id}>{item.name}</option>
                                        })}
                                    </Form.Control>
                                </Col>

                                <Col md={3}>
                                    <Form.Label>Is Active</Form.Label>
                                    <Form.Control name='isActive' type="number" value={filterData.isActive} onChange={_filterDataChange} as="select">

                                        <option value='' >Not Selected</option>
                                        <option value='true' >Yes</option>
                                        <option value='false' >No</option>

                                    </Form.Control>
                                </Col>
                            </Row>
                            <br />
                            <Row>
                                <Col md={3}>
                                    <Form.Label>Is Canceled</Form.Label>
                                    <Form.Control name='isCanceled' type="number" value={filterData.isCanceled} onChange={_filterDataChange} as="select">

                                        <option value='' >Not Selected</option>
                                        <option value='true' >Yes</option>
                                        <option value='false' >No</option>

                                    </Form.Control>
                                </Col>
                            </Row>
                            <Row style={{ display: 'flex', justifyContent: 'flex-end' }} >
                                <button
                                    className="btn btn-outline-secondary btn-sm mr-2"
                                    onClick={_resetFilters}
                                    style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                                >Reset Filters</button>
                                <button
                                    className="btn btn-secondary btn-sm mr-4"
                                    onClick={_search}
                                    style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                                >Apply Filters</button>

                            </Row>

                        </Card.Body>

                    </div>
                </Collapse>

            </Card.Header>
        </>
    )
}

export default withRouter(InvoicesFilter);