import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';


import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { getInvoices, deleteInvoices } from '../../api/sales/invoice';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import isEmpty from '../../validation/isEmpty';
import InvoicesFilter from '../../App/components/SearchFilters/InvoicesFilter';
import { getInvoicesSearchFilterObject } from '../../helpers/getSearchFilterObject'
import Num from '../../App/components/Reusables/Num';
import { getStartEnd } from '../../helpers/getStartEnd';

const Invoices = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [invoicesArr, setInvoicesArr] = React.useState([]);
    const [instanceCount, setInstanceCount] = React.useState(0)

    const [deleteData, setDeleteData] = React.useState({ showModal: false, invoiceId: null })
    // const match = React.useMatch()

    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getInvoicesSearchFilterObject(props),
            ...getStartEnd(props,)
        }
        console.log(JSON.stringify(requestJson))
        getInvoices(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setInvoicesArr([...res.json.invoices]);
                    setInstanceCount(res.json.count);
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });

        return () => controller.abort();
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ ...deleteData, showModal: true, purchaseOrderId: id })
    }


    //here delete corresponds to cancel of invoice cause invoice can only be canceled
    const _delete = (idToBeDeleted) => {
        console.log(idToBeDeleted);
        deleteInvoices({ action: 'delete', invoices_id: [idToBeDeleted] })
            .then(async (res) => {
                if (res.json.status) {
                    const deletedInvoice = invoicesArr.find(inv => inv.id === idToBeDeleted);
                    let tempArr = invoicesArr;
                    tempArr.splice(tempArr.indexOf(deletedInvoice), 1);
                    await setInvoicesArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, invoiceId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Invoice deleted successfully!', background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }


    const InvoicesList = invoicesArr.map((invoice, index) => {
        return (
            <tr key={index}

                style={{ cursor: 'pointer' }}
                onClick={(e) => {
                    props.history.push('/invoice/' + invoice.id)
                }}
            >
                <td>{invoice.invoice_number}</td>

                <td>{invoice.customer_name}</td>
                <td style={{ maxWidth: '200px', overflow: 'hidden', paddingRight: '50px' }}>{invoice.items}</td>
                <td>{(new Date(invoice.invoiced_on)).toDateString()}</td>
                <td>{invoice.total_weight} {invoice.weight_unit}</td>
                <th scope="row">Rs. <Num>{invoice.total_amount}</Num></th>
                <td>{invoice.status_name}</td>

                <td>
                    {invoice.is_canceled || invoice.status_is_sold ?
                        null :

                        <i title="Update"
                            className="feather icon-edit text-c-green f-19 m-r-10"
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                e.stopPropagation();
                                props.history.push('/invoice/update/' + invoice.id)
                            }
                            }
                        />
                    }
                </td>
            </tr>
        )
    })

    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Invoices</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`/invoice/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <InvoicesFilter />
                            <Table hover responsive>
                                <thead>
                                    <tr>
                                        <th>Invoice Number</th>
                                        <th>Customer Name</th>
                                        <th>Sold By</th>
                                        <th>Invoiced On</th>
                                        <th>Total Weight</th>
                                        <th>Invoice Total</th>
                                        <th>Status</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {InvoicesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(invoicesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >
                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.invoiceId)}
                        redTitle={"Cancel Invoice"}
                    />
                </Col>
            </Row>
        </>
    );
}

export default Invoices;